<template>
  <div class="b">
    <div style="height: 50px; line-height: 50px">
      <h1 class="ti">我加入的群组</h1>
      <p class="join-total">{{ groups.length }}</p>
    </div>
    <hr />
    <!-- 没有加入任何群组 -->
    <h1 class="msg" v-if="msg != ''">{{ msg }}</h1>

    <div v-for="group in groups" :key="group.groupID" class="g">
      <router-link
        :to="{
          name: 'native',
          query: {
            groupID: group.groupID,
            leaderID: group.leaderID,
            groupName: group.groupName,
            headImageAddress: group.headImageAddress,
          },
        }"
      >
        <img :src="group.headImageAddress" />
      </router-link>
      <router-link
        :to="{
          name: 'native',
          query: {
            groupID: group.groupID,
            leaderID: group.leaderID,
            groupName: group.groupName,
            headImageAddress: group.headImageAddress,
          },
        }"
      >
        <h1>{{ group.groupName }}</h1>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "Block",
  data() {
    return {
      msg: "",
      init: false,
    };
  },
  methods: {
    ...mapActions("group", ["initGroups"]),
  },
  computed: {
    ...mapState("group", ["groups", "host", "users"]),
  },
  activated() {
    if (!this.init) {
      this.$axios
        .get("http://localhost:8080/list/group", {
          params: {
            userID: this.host.userID,
          },
        })
        .then((res) => {
          if (!res.data) {
            this.msg = "你没有加入任何群组";
          } else {
            this.initGroups(res.data);
          }
          this.init = true;
        })
        .catch((res) => {
          this.msg = "请求出现异常,请检查网络环境";
        });
    }
  },
};
</script>

<style scoped>
.msg {
  display: inline-block;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: rgba(0, 0, 0, 0.3);
}
.b {
  float: left;
  margin-left: 100px;
  width: 980px;
  height: 500px;
  background-color: #fff;
  overflow: hidden;
}

.ti {
  float: left;
  margin-left: 20px;
  font-size: 16px;
  font-weight: normal;
  color: #212121;
}

.join-total {
  float: left;
  position: absolute;
  margin-left: 136px;
  color: #626262;
  font-size: 14px;
}

.g {
  float: left;
  margin-left: 16px;
  margin-top: 8px;
}

.g img {
  width: 225px;
  height: 130px;
  vertical-align: middle;
  background-color: red;
}

.g h1 {
  font-size: 14px;
  color: #212121;
  text-align: center;
  font-weight: normal;
  margin: 10px;
  overflow: hidden;
  width: 205px;
  text-overflow: ellipsis;
  height: 22px;
  white-space: nowrap;
}
</style>